import PropTypes from "prop-types"
import React from "react"
import styled from 'styled-components'


const Project = ({ title, gif, info, technologies, link, index }) => {

  const Container = styled.div`
  width: 40%;
  margin-right: ${props => props.index % 2 === 0 ? "60%" : "0%"};
  margin-left: ${props => props.index % 2 === 0 ? "0%" : "60%"};
  margin-bottom: -10em;
  height: 400px;
  background-color: lightblue;
  @media (max-width: 700px) {
    height: 100%;
    width: 100%;
    padding-left: 0;
    padding-right: 0;
    margin-bottom: 0;
  }
  `
  const TitleContainer = styled.div`
  position: relative;
  top: 10px;
  left: 50px;
  font-size: 30px;
  background-color: beige;
  `

  const GifContainer = styled.div`
  height: 80%;
  width: 80%;
  background-color: grey;
  position: relative;
  top: 30px;
  z-index: 10;
  `

  const InfoContainer = styled.div`
  height: 70%;
  width: 80%;
  position: relative;
  bottom: 20px;
  background-color: white;
  z-index: 1;
  &:hover {
    z-index: 20;
  }
  `

  return (
    <Container index={index}>
      <TitleContainer>title: {title}</TitleContainer>
      <GifContainer>
        img:
        <img src={gif} alt="gif"></img>
      </GifContainer>
      <InfoContainer>info: {info}</InfoContainer>
    </Container>
  )
}

Project.propTypes = {
  siteTitle: PropTypes.string,
}

Project.defaultProps = {
  siteTitle: ``,
}

export default Project
